import data from "../../data.json"
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data(){
		return {
			item:{},
			empty:true,
			up:true
		}
	},
	components:{
		uniIcons
	},
	onLoad(option){
		let id = option.id - 1
		this.item = data[id]
	},
	methods:{
		// {param:number} 为宠物点赞，这里需要数据库来关联用户和宠物
		clickmain(param){
			if(param == 2){
				this.up = !this.up
				this.item.num--
			} else if(param == 3){
				this.up = !this.up
				this.item.num++
			}else{
				this.empty = false
				this.up = false
				this.item.num++
			}
		},
		//打波电话
		callPthone(){
			console.log(this.item.phone)
			uni.makePhoneCall({
				phoneNumber: this.item.phone.toString(),
				success(){
					console.log("正在拨打电话")
				},
				fail(err){
					console.log(err)
				}
			})
		},
		//得到它
		getIt(){
			this.$store.commit("setPet",this.item)
			uni.setStorage({
				key:"pet",
				data:this.$store.state.pet,
				success:()=>{
					uni.switchTab({
						url:`../shoppingCar/shoppingCar`,
						success:()=>{
							uni.showToast({
								title:`已将${this.item.name}加入想要列表`
							})
						}
						
					})
				},
				fail:function(err){
					console.log(err)
				}
			})
		},
		//back
		back(){
			uni.navigateBack({
				  url: '../main/main',
					fail(err){
						console.log(err)
					}
			})
		}
	}
}
